.c-container__wrapper {
  background-size: cover;
  background-color: darken($color-bg-grey, 10%);
  overflow: hidden;
  width: map-get($main-sizes, max-width);
  height: map-get($main-sizes, max-height);

  .c-container {
    padding-left: map-get($main-sizes, padding-left);
    padding-right: map-get($main-sizes, padding-right);
    padding-top: map-get($main-sizes, padding-top)/2;

    &::before {
      $size: 1440px;
      @include overlay($size, $color-black, 0.3);
      top: -$size/2.245;
      left: -$size*1.333;
    }

    .c-title {
      height: map-get($main-sizes, title-height);
    }

    .c-nav__wrapper {
      display: inline-block;
      width: map-get($main-sizes, nav-width);

      &::after {
        $size: 2000px;
        @include overlay($size, $color-black, 0.4);
        top: -$size/2.17;
        left: -$size*1.1;
      }
    }

    &.c-container--full .c-content {
      width: map-get($main-sizes, content-width) + map-get($main-sizes, nav-width);
    }

    .c-content {
      display: inline-block;
      vertical-align: top;
    }
  }
}



